
export async function strategy(network, provider, addresses, options, snapshot) {
  // need to put a try catch here otherwise the website doesn't display any errors
  try {
    const body = JSON.stringify([network, provider, addresses, options, snapshot])
    const fetchOptions = {body, method: 'POST', headers: {'Content-Type': 'application/json'}}
    const apiUrl = 'https://snapshot.eth4.org'
    // if (window.location.hostname === 'localhost') {
    //   apiUrl = 'http://localhost:8282'
    // }
    const fetchUrl = `${apiUrl}/strategies/rot-balance-all-cached`
    const res = await fetch(fetchUrl, fetchOptions).then(res => res.json())
    return res
  }
  catch (e) {
    console.error(e)
  }
}
