import { formatUnits } from '@ethersproject/units';
import { multicall } from '../../utils';
import abi from './abi/PoliceChief';

const policeChiefAddress = '0x669Bffac935Be666219c68D20931CBf677b8Fa1C'

export async function strategy(network, provider, addresses, options, snapshot) {
  try {
    const blockTag = typeof snapshot === 'number' ? snapshot : 'latest';
    const response = await multicall(
      network,
      provider,
      abi,
      addresses.map((address: any) => [policeChiefAddress, 'niceBalanceAll', [address]]),
      { blockTag }
    );
    console.log(response)
    return Object.fromEntries(
      response.map((value, i) => [
        addresses[i],
        parseFloat(formatUnits(value.toString(), options.decimals))
      ])
    );
  }
  catch (e) {
    console.error(e)
  }
}
