import { Contract } from '@ethersproject/contracts';
import { abi as multicallAbi } from '../../abi/Multicall.json';
import { BigNumber } from '@ethersproject/bignumber'
import pRetry from 'p-retry'

// we use regular bignumber.js to be able to handle floats
import BigNumberJs from 'bignumber.js'
BigNumberJs.config({EXPONENTIAL_AT: 1000, DECIMAL_PLACES: 80})

const MULTICALL = {
  1: '0xeefba1e63905ef1d7acba5a8513c70307c1ce441',
  4: '0x42ad527de7d4e9d9d011ac45b31d8551f8fe9821',
  5: '0x77dca2c955b15e9de4dbbcf1246b4b85b651e50e',
  6: '0x53c43764255c17bd724f74c4ef150724ac50a3ed',
  42: '0x2cc8688c5f75e365aaeeb4ea8d6a480405a48d2a',
  56: '0x1ee38d535d541c55c9dae27b12edf090c608e6fb',
  100: '0xb5b692a88bdfc81ca69dcb1d924f59f0413a602a'
}

const maxMulticallCount = 1000

async function multicallChunk(network, provider, calls, options?) {
  const multi = new Contract(MULTICALL[network], multicallAbi, provider);

  const encodedCalls = []
  for (const call of calls) {
    encodedCalls.push([
      call.contractAddress.toLowerCase(),
      call.interface.encodeFunctionData(call.functionName, call.functionArguments)
    ])
  }

  try {
    const [, response] = await multi.aggregate(
      encodedCalls,
      options || {}
    );
    return response.map((call, i) => {
      return calls[i].interface.decodeFunctionResult(calls[i].functionName, call)
    });
  } catch (e) {
    console.error(e)
    return Promise.reject();
  }
}

export async function multicall(network, provider, calls, options?) {
  calls = [...calls]

  const callsChunks = []
  while (calls.length) {
    const chunkSize = calls.length < maxMulticallCount ? calls.length : maxMulticallCount
    const callsChunk = calls.splice(0, chunkSize)
    callsChunks.push(callsChunk)
  }

  const multicallChunkPromises = []
  for (const callsChunk of callsChunks) {
    const multicallWithRetry = pRetry(() => multicallChunk(network, provider, callsChunk, options), {retries: 3})
    multicallChunkPromises.push(multicallWithRetry)
  }

  const multicallChunkResponses = await Promise.all(multicallChunkPromises)
  const responses = []
  for (const chunkResponses of multicallChunkResponses) {
    responses.push(...chunkResponses)
  }

  return responses
}

export const addBalances = (...balances) => {
  const totals = []
  for (const addressIndex in balances[0]) {
    let addressTotal = BigNumber.from(0)
    for (const balanceIndex in balances) {
      addressTotal = addressTotal.add(BigNumber.from(balances[balanceIndex][addressIndex].toString()))
    }
    totals.push(addressTotal)
  }
  return totals
}

// ------------------------
// calculate balance staked
// ------------------------

const calculateRotBalanceStakedInAPool = ({lpBalanceStaked, lpTotalSupply, uniswapPairRotBalance} = {}) => {
  // console.log('calculateRotBalanceStakedInAPool args', {lpBalanceStaked: lpBalanceStaked.toString(), lpTotalSupply: lpTotalSupply.toString(), uniswapPairRotBalance: uniswapPairRotBalance.toString()})

  let rotStakedByUserBalance
  if (lpBalanceStaked.toString() === '0' || lpTotalSupply.toString() === '0' || uniswapPairRotBalance.toString() === '0') {
    rotStakedByUserBalance = BigNumber.from(0)
  }
  else {
    // formula is: user lp staked balance / lp total supply * rot balance of uniswap pair
    // we use regular bignumber.js to be able to handle floats
    const percentOfTotalLpSupplyStakedByUser = new BigNumberJs(lpBalanceStaked.toString()).div(new BigNumberJs(lpTotalSupply.toString()))
    rotStakedByUserBalance = new BigNumberJs(uniswapPairRotBalance.toString()).times(percentOfTotalLpSupplyStakedByUser)
    // remove decimals to use ethers.js big number
    rotStakedByUserBalance = BigNumber.from(rotStakedByUserBalance.toString().split('.')[0])

    // console.log('calculateRotBalanceStakedInAPool return', {rotStakedByUserBalance: rotStakedByUserBalance.toString(), percentOfTotalLpSupplyStakedByUser: percentOfTotalLpSupplyStakedByUser.toString()})
  }

  return rotStakedByUserBalance
}

const calculateRotBalanceStakedInAllPools = ({userInfoResponses, uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses} = {}) => {
  // console.log('calculateRotBalanceStakedInAllPools args', {userInfoResponses, uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses})

  let rotBalanceStakedTotal = BigNumber.from(0)
  for (const [i, userInfo] of userInfoResponses.entries()) {
    const addressLpBalanceStaked = BigNumber.from(userInfo.amount.toString())
    const lpTotalSupply = BigNumber.from(uniswapPairTotalSupplyResponses[i].toString())
    const uniswapPairRotBalance = BigNumber.from(uniswapPairRotBalanceOfResponses[i].toString())
    const addressRotBalanceStaked = calculateRotBalanceStakedInAPool({
      lpBalanceStaked: addressLpBalanceStaked, lpTotalSupply, uniswapPairRotBalance
    })
    rotBalanceStakedTotal = rotBalanceStakedTotal.add(addressRotBalanceStaked)
  }

  // console.log('calculateRotBalanceStakedInAllPools return', {rotBalanceStakedTotal})
  return rotBalanceStakedTotal
}

export const calculateAddressesRotBalanceStaked = ({uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses, chefUserInfoResponses} = {}) => {
  // console.log('calculateAddressesRotBalanceStaked args', {uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses, chefUserInfoResponses})

  chefUserInfoResponses = [...chefUserInfoResponses]
  const chefRotPoolCount = uniswapPairTotalSupplyResponses.length

  const addressesRotBalanceStaked = []

  // loop over all chefUserInfoResponses
  while (chefUserInfoResponses.length) {
    const currentAddressUserInfoResponses = chefUserInfoResponses.splice(0, chefRotPoolCount)
    const addressRotBalanceStakedTotal = calculateRotBalanceStakedInAllPools(
      {userInfoResponses: currentAddressUserInfoResponses, uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses}
    )
    addressesRotBalanceStaked.push(addressRotBalanceStakedTotal)
  }

  // console.log('calculateAddressesRotBalanceStaked return', {addressesRotBalanceStaked})
  return addressesRotBalanceStaked
}

export default {
  multicall,
  addBalances,
  calculateAddressesRotBalanceStaked
}
