import { formatUnits } from '@ethersproject/units'
import { BigNumber } from '@ethersproject/bignumber'
import { Interface } from '@ethersproject/abi';
import { multicall, addBalances, calculateAddressesRotBalanceStaked } from './utils'
import erc20Abi from './abi/erc20.json'
import chefAbi from './abi/masterchef.json'
const erc20Interface = new Interface(erc20Abi)
const chefInterface = new Interface(chefAbi)

const decimals = 18
const chefAddress = '0x577af3dce5aaa89510135d7f6e095e33c06b8b1f'
const sushiAddress = '0xd04785c4d8195e4a54d9dec3a9043872875ae9e2'

// chefPoolCount needs to be edited every time a new pool is added
const chefPoolCount = 16
// chefRotPools need to be edited every time new rot pools are added
const chefRotPools = [
  {poolId: 7, uniswapPairAddress: '0x5A265315520696299fa1EcE0701c3a1BA961b888'},
  {poolId: 12, uniswapPairAddress: '0x5cfd4eE2886CF42C716BE1E20847bda15547C693'},
  {poolId: 13, uniswapPairAddress: '0xE488C45ADEa8d83E25EbC548F711CeF799E5A24E'},
  {poolId: 14, uniswapPairAddress: '0x39a13a5541112D24830BDE4b8CE581b7416c5239'},
  {poolId: 15, uniswapPairAddress: '0xb0B948f321Ddc5E1f138EDb158504fFAc375F21F'},
]

// ---------------
// pending harvest
// ---------------

const getPendingSushiCalls = (addresses) => {
  const calls = []
  for (const address of addresses) {
    let currentPoolId = 0
    while (currentPoolId < chefPoolCount) {
      calls.push({contractAddress: chefAddress, interface: chefInterface, functionName: 'pendingSushi', functionArguments: [currentPoolId, address]})
      currentPoolId++
    }
  }
  return calls
}

const getRotBalancesPendingHarvest = (pendingSushiResponses) => {
  pendingSushiResponses = [...pendingSushiResponses]
  const totalsPending = []
  while (pendingSushiResponses.length) {
    let poolId = 0
    let totalPending = BigNumber.from(0)
    while (poolId++ < chefPoolCount) {
      const pendingSushiResponse = pendingSushiResponses.shift()
      totalPending = totalPending.add(BigNumber.from(pendingSushiResponse.toString()))
    }
    totalsPending.push(totalPending)
  }
  return totalsPending
}

// -----------
// rot balance
// -----------

const getRotBalanceOfCalls = (addresses) => {
  const calls = []
  for (const address of addresses) {
    calls.push({contractAddress: sushiAddress, interface: erc20Interface, functionName: 'balanceOf', functionArguments: [address]})
  }
  return calls
}

// ------------------
// rot balance staked
// ------------------

const getUniswapPairTotalSupplyCalls = () => {
  const calls = []
  for (const rotPool of chefRotPools) {
    calls.push({contractAddress: rotPool.uniswapPairAddress, interface: erc20Interface, functionName: 'totalSupply', functionArguments: []})
  }
  return calls
}

const getUniswapPairRotBalanceOfCalls = () => {
  const calls = []
  for (const rotPool of chefRotPools) {
    calls.push({contractAddress: sushiAddress, interface: erc20Interface, functionName: 'balanceOf', functionArguments: [rotPool.uniswapPairAddress]})
  }
  return calls
}

const getChefUserInfoCalls = (addresses) => {
  const calls = []
  for (const address of addresses) {
    for (const rotPool of chefRotPools) {
      calls.push({contractAddress: chefAddress, interface: chefInterface, functionName: 'userInfo', functionArguments: [rotPool.poolId, address]})
    }
  }
  return calls
}

// --------
// strategy
// --------

export async function strategy(network, provider, addresses, options, snapshot) {
  // need to put a try catch here otherwise the website doesn't display any errors
  try {
    const blockTag = typeof snapshot === 'number' ? snapshot : 'latest';

    const rotBalanceOfCalls = getRotBalanceOfCalls(addresses)
    const pendingSushiCalls = getPendingSushiCalls(addresses)
    const uniswapPairTotalSupplyCalls = getUniswapPairTotalSupplyCalls()
    const uniswapPairRotBalanceOfCalls = getUniswapPairRotBalanceOfCalls()
    const chefUserInfoCalls = getChefUserInfoCalls(addresses)

    const calls = [...rotBalanceOfCalls, ...pendingSushiCalls, ...uniswapPairTotalSupplyCalls, ...uniswapPairRotBalanceOfCalls, ...chefUserInfoCalls]

    // console.log({addresses: addresses.length, calls: calls.length})

    const response = await multicall(
      network,
      provider,
      calls,
      { blockTag }
    )

    const erc20BalanceOfResponses = response.splice(0, rotBalanceOfCalls.length)
    const pendingSushiResponses = response.splice(0, pendingSushiCalls.length)
    const uniswapPairTotalSupplyResponses = response.splice(0, uniswapPairTotalSupplyCalls.length)
    const uniswapPairRotBalanceOfResponses = response.splice(0, uniswapPairRotBalanceOfCalls.length)
    const chefUserInfoResponses = response.splice(0, chefUserInfoCalls.length)

    // calculate balances from responses
    const rotBalances = erc20BalanceOfResponses
    const rotBalancesPendingHarvest = getRotBalancesPendingHarvest(pendingSushiResponses)
    const rotBalancesStaked = calculateAddressesRotBalanceStaked({uniswapPairTotalSupplyResponses, uniswapPairRotBalanceOfResponses, chefUserInfoResponses})

    // console.log({rotBalances, rotBalancesPendingHarvest, rotBalancesStaked})

    const rotBalancesAll = addBalances(rotBalances, rotBalancesPendingHarvest, rotBalancesStaked)
    return Object.fromEntries(
      rotBalancesAll.map((value, i) => [
        addresses[i],
        parseFloat(formatUnits(value.toString(), decimals))
      ])
    )
  }
  catch (e) {
    console.error(e)
  }
}
